<template>
    <div>
        <div class="shebei-title">
            <div class="btns fl">
                <Button :type="col1" size="large" @click="toChaxun">查询</Button>
                <Button :type="col2" size="large" @click="toWeihu">维护</Button>
                <Button :type="col3" size="large" @click="toTongji">统计</Button>
            </div>
            <span class="user-name fr">
                <span class="user-text"> admin,</span>
                <span style="margin-right:34px;">你好</span>
                <Button type="primary" @click="back">返回</Button>
            </span>
        </div>
    </div>
</template>
<script>
    export default {
        name: '',
        data() {
            return {
                col1: 'primary',
                col2: '',
                col3: '',
            }
        },
        methods: {
            back: function () {
                this.$store.commit('suoqi', '74%');
                this.$router.push('home');
            },
            toWeihu: function (params) {
                this.col1 = '';
                this.col3 = '';
                this.col2 = 'primary';
            },
            toTongji: function (params) {
                this.col1 = '';
                this.col2 = '';
                this.col3 = 'primary';
            },
            toChaxun: function (params) {
                this.col3 = '';
                this.col2 = '';
                this.col1 = 'primary';
            }
        }
    }
</script>
<style lang='less' scoped>
    .shebei-title {
        height: 80px;
        line-height: 80px;
        border-bottom: 1px solid #000;
        padding: 0 20px;
        .btns button {
            width: 180px;
        }
    }

    .user-name {
        font-size: 14px;
    }

    .user-text {
        color: #0000FF;
    }

    .exit {
        margin-left: 40px;
    }
</style>