<template>
    <div class="sb-search fl" :style="{width:width}">
        <div class="shebeiType">
            <div class="fl">
                <span class="sel-name">设备类型:</span>
                <Select v-model="model1" style="width:150px" size="small" placeholder="">
                    <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
            </div>
        </div>
               
        <div class="search-con">
            <ul>
                <li style="height:34px;">
                    <span class="sel-name">变电站名称:</span>
                    <Input size="small" v-model="value" placeholder="" style="width: 130px" />
