                <li style="height:34px;">
                    <span class="sel-name">电压等级:</span>
                    <Select v-model="model1" style="width:100px" size="small" placeholder="请选择电压等级">
                        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </li>
                <li style="height:34px;">
                    <span class="sel-name">厂家:</span>
                    <Select v-model="model1" style="width:80px" size="small" placeholder="请选择厂家">
                        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </li>
                <li style="height:34px;">
                    <span class="sel-name">型号:</span>
                    <Select v-model="model1" style="width:80px" size="small" placeholder="请选择型号">
                        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </li>
                <li style="height:34px;">
                    <span class="sel-name">通道类型:</span>
                    <Select v-model="model1" style="width:100px" size="small" placeholder="请选择通道类型">
                        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </li>
                <li style="height:34px;" @click="timeShow">
                    <span class="fl">日期：</span>
                    <Input size="small" :readonly="true" clearable icon="ios-clock-outline" v-model="datas.startDate" placeholder="请选择开始时间" style="width: 130px"></Input>
                    <calendar v-model="defaultDate" @change="demo1DateChange">
                    </calendar>
                </li>
                <li style="height:34px;" @click="timeShow2">
                    <span class="fl" style="margin-left:10px">至：</span>
                    <Input size="small" :readonly="true" clearable icon="ios-clock-outline" v-model="datas.endDate" placeholder="请选择结束时间" style="width: 130px"></Input>
                    <calendar v-model="defaultDate2" @change="demo1DateChange2">
                    </calendar>
