                <!-- <li>
                    <Button type="primary" size="small" @click="showTiaojian">编辑查询条件</Button>
                </li> -->
                <li>
                    <Button type="primary" size="small"  @click="showTiaojian">查询</Button>
                </li>
            </ul>
        </div>

        <Modal width="300" title="请选择需要查询的字段" v-model="seletct_show" @on-ok="ok" @on-cancel="cancel">
            <ul class="tiaojian">
                <li>
                    <Checkbox v-model="single">变电站名称</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">投运日期</Checkbox>
