                </li>
                <li>
                    <Checkbox v-model="single">保护装置类型</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">定值单号及照片</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">厂家</Checkbox>
                </li>
                <li>
