                </li>
                <li>
                    <Checkbox v-model="single">压板照片</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">通道类型</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">交换机照片</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">出厂日期</Checkbox>
                </li>
                <li>
                    <Checkbox v-model="single">装置面板</Checkbox>
                </li>
