<script>
    export default {
        name: '',
        data() {
            return {
                single: false,
                seletct_show: false,
                datas: {
                    startDate: "",
                    endDate: ""
                },
                defaultDate: false,
                defaultDate2: false,
                width: "74%",
                cityList: [{
                        value: 'New York',
                        label: 'New York'
                    },
                    {
                        value: 'London',
                        label: 'London'
                    },
                    {
                        value: 'Sydney',
                        label: 'Sydney'
                    },
                    {
                        value: 'Ottawa',
                        label: 'Ottawa'
                    },
                    {
                        value: 'Paris',
                        label: 'Paris'
                    },
                    {
                        value: 'Canberra',
                        label: 'Canberra'
                    }
                ],
                model1: '',
                value: ''
            }
        },
        methods: {
            demo1DateChange(date, formatDate) {
                this.datas.startDate = formatDate;
            },
            timeShow: function(params) {
                this.defaultDate = true;
            },
            demo1DateChange2(date, formatDate) {
                this.datas.endDate = formatDate;
            },
            timeShow2: function (params) {
                this.defaultDate2 = true;
            },
            showTiaojian: function (params) {
                this.seletct_show = true;
            },
        },
        computed: {
            getWidth() {
                return this.$store.state.shebeiWidth;
            },
        },
        watch: {
            getWidth(val) {
                this.width = val;
            },
        }
    }
</script>
<style lang='less' scoped>
    .sb-search {
        width: 74%;
    }

    .shebeiType {
        height: 46px;
        line-height: 46px;
        width: 100%;
        padding-left: 14px; // border-bottom: 2px solid #dcdee2;
        border-left: 2px solid #dcdee2;
    }

    .sel-name {
        margin-right: 12px;
    }

    .search-con {
        padding: 10px;
        overflow: hidden;
        border: 2px solid #dcdee2;
        ul li {
            float: left;
            margin-right: 14px;
            height: 32px;
            line-height: 32px;
        }
    }

    .tiaojian {
        li {
            float: left;
            width: 110px;
            margin-right: 10px;
            margin-bottom: 10px;
        }
        overflow: hidden;
        margin-left: 20px;
    }
</style>
