import Vue from 'vue'
import Router from 'vue-router'
// 主路由
import main_routes from './luyou/main_routes';
// 设备模块路由
import shebei_routes from './luyou/shebei_routes';
// 运检模块路由
import yunjian_routes from './luyou/yunjian_routes';

Vue.use(Router)
let Routes = new Set([...main_routes, ...shebei_routes,...yunjian_routes])

export default new Router({
    base: process.env.BASE_URL,
    routes: Routes
})
